.hero {
  margin: 40px 95px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* margin-top: 50px; */
  gap: 1rem;
}

.line {
  /* Planet */
  z-index: -1;
  position: absolute;
  min-width: 365px;
  height: 365px;
  right: 15%;
  top: 200px;

  background: linear-gradient(
    180deg,
    #2fb8f2 0%,
    rgba(75, 37, 245, 0) 100%,
    #617ba3 100%
  );
  filter: blur(50px);
  transform: rotate(180deg);
}

.line2 {
  /* Eclipse */
  z-index: -1;
  position: absolute;
  min-width: 500px;
  height: 500px;
  right: 10%;
  top: 200px;

  background: linear-gradient(
    180deg,
    #7b96ff 0%,
    rgba(111, 195, 255, 0.46) 100%
  );
  filter: blur(80px);
  transform: rotate(180deg);
}

.stars1 {
  position: relative;
  top: -400px;
  left: 160px;
}

.stars2 {
  position: relative;
  top: -400px;
  left: 160px;
}

.container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.bg {
  width: 80%;
  overflow: hidden;
  position: absolute;
  z-index: -5;
  top: 18%;
  right: 10%;
}

.salut {
  position: relative;
  bottom: -20px;
  font-family: Manrope;
  font-size: 0.8rem;
  font-weight: 700;
  line-height: 22.4px;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  color: #0e89fc;
}
